
'use client';

import type { Building, User, Competition } from '@/types';
import { useState, useMemo } from 'react';
import BuildingCard from '@/components/minecraft/BuildingCard';
import WorksFilterControls from '@/components/filters/WorksFilterControls';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Inbox } from 'lucide-react';

interface CompetitionWorksDisplayProps {
  initialWorks: Building[];
  currentUser: User | null;
  competition: Competition;
}

export default function CompetitionWorksDisplay({
  initialWorks,
  currentUser,
  competition,
}: CompetitionWorksDisplayProps) {
  const [displayedWorks, setDisplayedWorks] = useState<Building[]>(initialWorks);

  const competitionYears = useMemo(() => {
    if (!initialWorks || initialWorks.length === 0) return [];
    const years = new Set(initialWorks.map(work => work.eventYear).filter(year => year != null));
    return Array.from(years).sort((a,b) => b - a);
  }, [initialWorks]);

  return (
    <div>
      <WorksFilterControls
        initialWorks={initialWorks}
        onFilteredWorks={setDisplayedWorks}
        showYearFilter={competitionYears.length > 1} 
        availableYears={competitionYears}
      />
      {displayedWorks.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6">
          {displayedWorks.map((building) => (
            <BuildingCard 
              key={building.id} 
              building={building} 
              currentUser={currentUser} 
            />
          ))}
        </div>
      ) : (
        <Alert className="border-primary/30 bg-primary/5 text-center py-10 mt-6">
          <Inbox className="h-12 w-12 text-primary mx-auto mb-4" />
          <AlertTitle className="text-xl font-semibold text-primary/90 mb-2">
            {initialWorks.length > 0 ? '没有匹配的作品' : `此活动 (${competition.name}) 暂无作品`}
          </AlertTitle>
          <AlertDescription className="text-primary/80">
            {initialWorks.length > 0 ? '请尝试调整搜索或筛选条件。' : '该活动可能还没有作品被记录。'}
          </AlertDescription>
        </Alert>
      )}
    </div>
  );
}
